var React = require('react');
var ReactDOM = require('react-dom');
var bootstrap = require('react-bootstrap');

var pullRight = {
	float: "right",
	marginRight: "5%"
};

var footStyle = {
	position: "absolute",
	bottom: "0",
	height: "50px",
	width: "100%",
	borderTop: "1px solid #ccc"
};

var centerDiv = {
	textAlign: "center",
	margin: "0 auto"
};

var roundButton = {
	marginTop: "5px",
	padding: "5px 0 0 2px",
	textAlign: "center",
	width: "35px",
	height: "35px",
	borderRadius: "50%"
};

var ContactBox = React.createClass({
	LoadContactsFromServer: function(){
		$.ajax({
			url: this.props.url,
			dataType: 'json',
			cache: false,
			success: function(data){
				this.setState({data: data});
			}.bind(this),
			error: function(xhr, status, err){
				console.log(this.props.url, status, err.toString());
			}.bind(this)
		});
	},

	getInitialState: function() {
    return {data: []};
  },

	componentDidMount: function(){
		this.LoadContactsFromServer();
	},

	render: function(){
		return (
			<div>
				<ContactHeader></ContactHeader>
				<ContactList data={this.state.data}></ContactList>
				<ContactFoot></ContactFoot>
			</div>
		);
	}
});

var ContactHeader = React.createClass({
	render: function(){
		return (
			<bootstrap.Navbar toggleNavKey={0}>
				<bootstrap.NavBrand>G-Address</bootstrap.NavBrand>
				<bootstrap.CollapsibleNav eventKey={0}>
					<bootstrap.Nav navbar right>
						<bootstrap.NavItem href="/login" eventKey={1}>登录</bootstrap.NavItem>
					</bootstrap.Nav>
				</bootstrap.CollapsibleNav>
			</bootstrap.Navbar>
		);
	}
});

var ContactList = React.createClass({
	render: function(){
		var contactNodes = this.props.data.map(function(contact, index){
			return (
				<Contact key={index}>{contact.name}</Contact>
			);
		});

		return (
			<bootstrap.ListGroup>
				{contactNodes}
			</bootstrap.ListGroup>
		);
	}
});

var Contact = React.createClass({
	render: function(){
		return (
			<bootstrap.ListGroupItem href="#">
				{this.props.children}
				<span><bootstrap.Glyphicon glyph="chevron-right" style={pullRight}/></span>
			</bootstrap.ListGroupItem>
		);
	}
});

var ContactFoot = React.createClass({
	render: function(){
		return (
			<div style={footStyle}>
				<div style={centerDiv}>
					<bootstrap.Button href="/people/new" bsSize="large" bsStyle="info" style={roundButton}>
						<bootstrap.Glyphicon glyph="plus" />
					</bootstrap.Button>
				</div>
			</div>
		);
	}
});

ReactDOM.render(<ContactBox url="/contacts/data" />, document.getElementById("content"));


